body {
  height: 3000px !important;
  width: 100vw !important;
  background-color: black;
}
#Navbar {
  width: 100%;
  height: 70px;
  z-index: 2;
  background-color: white;
  /* border: 1px solid black; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
#naming {
  height: 70px;
  width: 30%;
  /* border: 1px solid black; */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
}
#naming > strong:hover {
  color: blue;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}
#navigation {
  height: 70px;
  width: 70%;
  /* border: 1px solid black; */
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
}
#navigation > ul {
  list-style: none;
  margin: 0px;
  width: 70%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

/* Home component */
#Layout {
  height: 600vh;
  width: 100vw;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.div-all{
  height: 100vh;
  width: 100vw;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border: 1px solid white;
}
